<template>
    <s-header name="体重/体脂" rightName="历史数据" :rightPress="passHistoryData"  />
  <change-device fromPage="bloodSugar" :backgroundColor="'#06B48D'" :otherAddClick="add"/>
  <div class="box-weight">
          <div class="weight" >
              <weight-cont :detail= "detail" />
              <card v-for="(i, index) in list" :key="index" :item="i"/>
          </div>
    </div>
</template>

<script>
import {onMounted, reactive, toRefs, ref } from "vue";
import AccessoryHeader from "@/components/AccessoryHeader.vue";
import weightCont from "../component/weightCont.vue"
import sHeader from '@/components/SimpleHeader.vue';
import Card from "../component/card.vue";
import { useRouter } from "vue-router";
import ChangeDevice from "@/views/health-track/components/ChangeDevice";
import { getHomeDetial } from "@/service/weight"
import state from "@/store/state";
export default {

    setup(){
        onMounted(()=> {
          requestDetial()
        })
        const state = reactive({
          detail:{},
          list:[]

        })
        let {push} = useRouter();
        function passHistoryData (){
            push("/weightHistoryData")
        }

        // 请求首页详情数据
        const requestDetial = async () => {
         const res = await getHomeDetial()
          const { code, data } = res
          if(code === 200){
            state.detail = data
            const buTitleContentList = data.buTitleContentList || []
            buTitleContentList.unshift({
              label: "数据解读",
              desc:  [data.typeStr]
            })
            state.list = buTitleContentList
            console.log("state.list",state.list,buTitleContentList)
          }
        }
        const add = () => {
          push("/add-weight-height")
        }
        return {
          ...toRefs(state),
            passHistoryData,
             add
        }
    },
    components:{
        ChangeDevice,
        weightCont,
        sHeader,
        Card,
    }
}
</script>

<style lang="less" >
.box-weight{
  padding: 10px;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f2f2f2;
}
</style>
