<template>
    <div class="circle-box" >
        <v-circle
            layer-color="#ccc"
            :color="sData.bmi ?'#F7A369':'#ccc'"
            :stroke-width="50"
            :size="82"
            :current-rate="100"
        >
            <template #default >
                <div class="circle-text" >
                    <div>BMI指数</div>
                    <div>{{ sData.bmi  || '一一'}}</div>
                </div>
            </template>
        </v-circle>
        <v-circle
            layer-color="#ccc"
            :color="sData.weight ?'#F7A369':'#ccc'"
            :stroke-width="50"
            :size="133"
            style="margin: 0 10px"
            :current-rate="100"
        >
            <template #default >
                <div class="circle-text" >
                    <div>体重</div>
                    <div>{{ sData.weight  || '一一'}}</div>
                    <div>kg</div>
                </div>
            </template>
        </v-circle>
        <v-circle
            layer-color="#ccc"
            :color="sData.bfp ? '#F7A369':'#ccc'"
            :stroke-width="50"
            :size="82"
            :current-rate="100"
        >
            <template #default >
                <div class="circle-text" >
                    <div>体脂率</div>
                    <div>{{ sData.bfp || '一一'}}%</div>
                </div>
            </template>
        </v-circle>
    </div>
</template>

<script>
import {Circle} from "vant";
import {reactive, toRefs, watch} from "vue";
export default {
    name: "CircleGroup",
    components: {
        vCircle: Circle
    },
  props:{
    data: Object
  },
  setup(props){
    const state = reactive({
      sData:{}
    })
    watch(()=>props.data,(news,old)=>{
      state.sData = news
    })
    return {
      ...toRefs(state)
    }
  }
}
</script>

